.stack {
  margin-top: 300px;
  box-sizing: border-box;
  padding-top: 60px;
  z-index: 0;
}
.stack p {
  color: var(--day-sky-blue);
  margin: 0;
  line-height: 24px;
}
.stack .title {
  color: var(--cornflower-blue);
  margin: 0;
  font-size: 32px;
  text-decoration: none;
}
.stack .information {
  margin-top: 20px;
}
.stack .technology {
  margin-top: 10px;
  margin-bottom: 10px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 10px;
}
.stack .technology p {
  position: relative;
  padding-left: 20px;
  display: flex;
  align-items: center;
}
.stack .technology p::before {
  content: "";
  position: absolute;
  width: 10px;
  height: 10px;
  background-color: var(--cornflower-blue);
  border-radius: 50%;
  left: 0;
  top: 50%;
  transform: translate(0, -50%);
}
@media (max-width: 800px) {
  .stack .technology {
    grid-template-columns: 1fr 1fr 1fr;
  }
}
@media (max-width: 400px) {
  .stack .technology {
    grid-template-columns: 1fr 1fr;
  }
}